import React from 'react';
import { max } from 'd3-array';
import { curveBasis } from '@visx/curve';
import { AxisBottom } from '@visx/axis';
import { ParentSize } from '@visx/responsive';
import { Group } from '@visx/group';
import { LinePath } from '@visx/shape';
import { scaleBand, scaleLinear } from '@visx/scale';
import {getUrlParams, zipmap} from "./utils";
import './DistroPlot.scss';

export const NEIGHBOR_COLORS = {
    'c': '#ff931e',
    'a': '#a064ff',
    'n': '#3fa9f5',
    'l': '#ff3fa0',
};

const scalePadding = 32;

interface PriceValue {
    price: string
    value: number
    key: string
}

const params = getUrlParams();
const axisTicks = params['ax'];
const nPoints = axisTicks.length;
const valueKeys = Object.keys(params).filter((k) => k !== 'ax');
const valueParams = zipmap(valueKeys, valueKeys.map((k) => params[k]));
const colorKeys = Object.keys(NEIGHBOR_COLORS);
const colorIndexMap = zipmap(colorKeys, colorKeys.map((_, i) => i))
const series = Object.keys(valueParams).map((k =>
    valueParams[k].map((v, i) => ({ key: k, price: axisTicks[i], value: parseInt(v) })))
).sort((a, b) => colorIndexMap[a.key] - colorIndexMap[b.key]);
const allData = series.reduce((rec, d) => rec.concat(d), []);

const getX = (d: PriceValue) => d.price;
const getY = (d: PriceValue) => d.value;

const xScale = scaleBand<string>({
    domain: axisTicks,
    paddingOuter: 1,
    paddingInner: 1,
});
const yScale = scaleLinear<number>({
    domain: [0, max(allData, getY) as number],
});

const tickLabelProps = () =>
({
    fontSize: 14,
    textAnchor: 'middle'
} as const);

const includeTick = (i: number) => {
    if (axisTicks.length % 2 === 0) {
        return i > 0 && i < axisTicks.length - 2 && i % 2 === 0;
    } else {
        return i > 1 && i < axisTicks.length - 1 && i % 2 === 0;
    }
}

const DistroPlot = () => (
    <ParentSize debounceTime={0} className='bg'>
        {({ width, height }) => {
            const scaleHeight = height - scalePadding;
            xScale.range([0, width + 24]);
            yScale.range([scaleHeight - 16, 0]);
            return (
                <svg width={width} height={height}>
                    {width > 8 &&
                    series.map((lineData, i) => {
                        return (
                            <Group key={`lines-${i}`} top={0} left={0}>
                                <LinePath<PriceValue>
                                    curve={curveBasis}
                                    data={lineData}
                                    x={d => xScale(getX(d)) ?? 0}
                                    y={d => yScale(getY(d)) ?? 0}
                                    stroke={NEIGHBOR_COLORS[lineData[0].key]}
                                    strokeWidth={5}
                                    strokeOpacity={0.875}
                                    shapeRendering="geometricPrecision"
                                />
                            </Group>
                        );
                    })}
                    <AxisBottom
                        axisClassName='axis'
                        hideAxisLine={true}
                        tickLength={2}
                        top={scaleHeight}
                        scale={xScale}
                        tickLabelProps={tickLabelProps}
                        tickValues={axisTicks.filter((_, i) => includeTick(i))}
                        numTicks={nPoints}
                    />
                </svg>
            );
        }
    }
    </ParentSize>
);

export default DistroPlot;